@charset "utf-8";
@import "common";
@import "fonts";

@mixin point-set ($width,$height,$sec,$delay) {
    position:absolute;
    width:$width;
    height:$height;
    border-radius:70%;
    background: radial-gradient(rgba(255, 255, 255, 1), transparent 50%);
    //animation:ani888 $sec $delay infinite alternate;
    //transform:translate($x,$y)
    animation-name:ani888 ;
    
}
@mixin div_img ($url) {
    background-image: url($url);
    background-size:cover;
    background-position:center;
}
