 @keyframes ani888{
    0%{
        opacity:0;
    }
    100%{opacity:1;}
 }
 @keyframes rotate {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);       
    }
}

  @keyframes tail {
    0% {
      width: 0;
    }
    
    30% {
      width: 400px;
    }
    
    100% {
      width: 0;
    }
  }
  
  @keyframes shooting {
    0% {
      transform: translateX(0);
    }
    
    100% {
      transform: translateX(1000px);
    }

  }
  
  @keyframes text_shadow {
    0% {
        text-shadow: 2px 2px 10px rgba(255, 255, 255, 0.9);     
    }
    
    100% {
        text-shadow: 2px 2px 15px rgba(255, 255, 255, 0.9);     
    }
  }

  @keyframes upDown {
    0% {
        opacity: 0.5;
        transform: translateY(-5px);
    }
    50% {
        opacity: 1;
        transform: translateY(5px);
    }
    
    100% {
        opacity: 0.5;
        transform: translateY(-5px);
    }
  }

  @keyframes twinkling {
    0% {
      box-shadow: 2px 2px 10px rgba(255, 255, 255, 0.5);
    }
  
    100% {
      box-shadow: 2px 2px 20px rgb(255, 255, 255, 0.7);
    }
  }
  
  @keyframes up {
    0% {
        transform: translateY(50px);
    }
    
    100% {
        transform: translateY(0px);
    }
  }
