#wrap > .swiper > .swiper-pagination{
    width:100%;
    position: absolute;
    display: flex;
    justify-content:flex-end;
    top: 40px !important;
}
#wrap > .swiper > .swiper-pagination > .swiper-pagination-bullet
{   
    margin-right:40px;
    width:unset;
    height:unset;   
    background-color:rgba(0, 255, 0, 0);
    border-radius:0%;
    text-align:left;
    color:#fff;
    font-size:1.2rem;
    font-family:'Noto Serif KR ExtraLight';
    opacity:1;
}
#wrap > .swiper > .swiper-pagination > .swiper-pagination-bullet-active{
    position: relative;
}
#wrap > .swiper > .swiper-pagination > .swiper-pagination-bullet-active::after{
    content: "";
    position: absolute;
    bottom: -7px;
    left:0;
    width: 100%;
    height: 2px;
    background: #fff;
    animation: aaa 0.8s forwards;
}

#slide4_box > .swiper > .swiper-button-prev::after{
    color:#595959 !important;
    font-weight: bold;
    font-size:3rem !important;
}
#slide4_box > .swiper > .swiper-button-next::after{
    color:#595959 !important;
    font-weight: bold;
    font-size:3rem !important;
}

#slide4_box > .swiper > .swiper-pagination > .swiper-pagination-bullet{
    width:1rem;
    height:1rem;
    background-color:#fff;
}

@keyframes aaa{
    0% {
        width: 0;
    }
    100% {
        width: 100%;
    }
}